import {
  useStaffCheckGuideQuery,
  useStaffActivateGuideMutation,
  useStaffGuideDetailQuery,
  useGuideMenteeListQuery,
  useGuideAddMenteeMutation,
  useGuideRemoveMenteeMutation,
  useGuideAddMeetingMutation,
  useGuideMeetingListQuery,
  useGuideMeetinDetailQuery,
  useGuideMeetingUpdateMutation,
  useGuideAddScheduleMeetingMutation,
  useGuideExportExcelListQuery,
  useGuideExportMenteeListMutation,
  useGuideExportAttendanceListMutation,
} from "../../../Services/GuideManager/guideManagerApiDynamic";

export const useStaffCheckGuide = (args) => {
  const { data, refetch, isFetching } = useStaffCheckGuideQuery(args.data, {
    skip: args.skip,
  });
  return {
    staffCheckGuide: data,
    staffCheckGuideRefetch: refetch,
    staffCheckGuideLoading: isFetching,
  };
};

export const useStaffActivateGuide = () => {
  const [staffActivateGuide] = useStaffActivateGuideMutation();
  return [staffActivateGuide];
};

