import { useEffect, useState } from "react";
import StaffMenteeCard from "./StaffMenteeCard";
import QvipleLoading from "../../../../Loader/QvipleLoading";
import StaffMentorTabsAttendance from "./StaffMentorTabsAttendance";
import style from "../DepartmentHead/Exminations/SeatingArrangement/SeatingArrangement.module.css";
import BorderBottom from "../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useTranslation } from "react-i18next";
import { useGuideMenteeList } from "../../../../optimized/Hooks/ApiHooks/GuideManager/guide-manager-api";

const StaffGuideMarkAttendance = ({ mid, onClose, onAddMeeting }) => {
  const { t } = useTranslation();
  const [present, setPresent] = useState([]);
  const [absent, setAbsent] = useState([]);
  const [isVisible, setIsVisible] = useState("PRESENT");

  const { guideMenteeList, guideMenteeListLoading } = useGuideMenteeList({
    data: {
      gid: mid,
      page: 1,
      limit: 2000,
      search: "",
    },
    skip: !mid,
  });

  const onPresentHandler = () => {
    let arr = [];
    for (let i = 0; i < guideMenteeList?.list?.length; i++) {
      arr.push(guideMenteeList?.list[i]?._id);
    }
    setAbsent(arr);
    setPresent([]);
    setIsVisible("PRESENT");
  };

  useEffect(() => {
    if (guideMenteeList?.list) {
      onPresentHandler();
    }
  }, [guideMenteeList?.list]);

  const onAbsentHandler = () => {
    let arr = [];
    for (let i = 0; i < guideMenteeList?.list?.length; i++) {
      arr.push(guideMenteeList?.list[i]?._id);
    }
    setAbsent([]);
    setPresent(arr);
    setIsVisible("ABSENT");
  };

  const onPresent = (val) => {
    if (present?.includes(val)) {
      setPresent((prev) => prev?.filter((id) => id !== val));
      setAbsent((prev) => [...prev, val]);
    } else {
      setPresent((prev) => [...prev, val]);
      setAbsent((prev) => prev?.filter((id) => id !== val));
    }
  };
  const onAbsent = (val) => {
    if (absent?.includes(val)) {
      setAbsent((prev) => prev?.filter((id) => id !== val));
      setPresent((prev) => [...prev, val]);
    } else {
      setAbsent((prev) => [...prev, val]);
      setPresent((prev) => prev?.filter((id) => id !== val));
    }
  };

  const onSave = () => {
    onAddMeeting({ present, absent });
  };
  return (
    <>
      <section
        className={style.seating_modal_title_left}
        style={{
          width: "100%",
          justifyContent: "space-between",
        }}
      >
        <div className={style.attendance_text}>
          <img
            src="/images/arrow-left-fees-icon.svg"
            onClick={onClose}
            alt=""
          />
          <h6>{t("mark_attendance")}</h6>
        </div>
        <div
          className={style.attendance_next_btn}
          style={{
            marginTop: "0",
            width: "80px",
            padding: "0.5rem",
            height: "auto",
            cursor: "pointer",
          }}
          onClick={onSave}
        >
          {t("add")}
        </div>
      </section>
      <BorderBottom
        customStyle={{
          width: "100%",
          marginBottom: "1rem",
        }}
      />
      <StaffMentorTabsAttendance
        onPresentHandler={onPresentHandler}
        onAbsentHandler={onAbsentHandler}
      />
      {guideMenteeList?.list?.map((mentee) => (
        <StaffMenteeCard
          mentee={mentee}
          key={mentee?._id}
          viewAs="ATTENDANCE"
          isVisible={isVisible}
          absent={absent}
          present={present}
          onPresent={onPresent}
          onAbsent={onAbsent}
        />
      ))}
      {guideMenteeListLoading && <QvipleLoading />}
    </>
  );
};

export default StaffGuideMarkAttendance;
